export var dmgResult = function (character, enemyStats) {
    var charStats = character.stats;
    var srcStat = character.srcStat;
    if (charStats === null) {
        return 0;
    }
    var AttackerLvl = 80;
    var defIncrease = 0;
    var baseDmg = (charStats["base".concat(srcStat)] * (1 + charStats["".concat(srcStat, "Increase")]) + charStats["flat".concat(srcStat)]) * charStats.multiplier;
    var dmgMultiplier = 1 + charStats.dmgIncrease;
    var totalDef = (200 + 10 * enemyStats.lvl) * (1 + defIncrease - (enemyStats.defReduction + charStats.defIgnore));
    if (totalDef < 0) {
        totalDef = 0;
    }
    var defMultiplier = 1 - (totalDef / (totalDef + 200 + 10 * AttackerLvl));
    var totalRes = enemyStats.res - enemyStats.resReduction - charStats.resPen;
    if (totalRes < -1) {
        totalRes = -1;
    }
    var resMultiplier = 1 - totalRes;
    var dmgTakenMultiplier = 1 + enemyStats.dmgTakenIncrease;
    return (Math.floor(baseDmg * dmgMultiplier * defMultiplier * resMultiplier * dmgTakenMultiplier));
    //Outgoing DMG = Base DMG * DMG% Multiplier * DEF Multiplier * RES Multiplier *
    //DMG Taken Multiplier * Universal DMG Reduction Multiplier * Weaken Multiplier
};
